.navbar {
  display: flex;
  justify-content: space-around;
  padding: 16px;
  box-shadow: 0 1px 3px -2px black;
}
.nav-logo {
  display: flex;
  align-items: center;
  gap: 10px;
}
.nav-f-logo {
  width: 200px;
  height: 50px;
  cursor: pointer;
}
.nav-logo p {
  color: rgb(61, 61, 61);
  font-size: 38px;
  font-weight: 600;
}
.nav-menu {
  display: flex;
  align-items: center;
  list-style: none;
  gap: 50px;
  color: rgb(0, 0, 0);
  font-size: 20px;
  font-weight: 500;
}
.nav-menu li {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 3px;
  cursor: pointer;
}
.nav-menu li a:visited {
  color: black;
}
.nav-menu li a:active {
  color: black;
}

.nav-menu hr {
  border: none;
  width: 80%;
  height: 2px;
  border-radius: 10px;
  background-color: rgb(1, 0, 0);
}
.nav-login-cart {
  display: flex;
  align-items: center;
  gap: 45px;
}
.nav-login-cart button {
  width: 157px;
  height: 58px;
  outline: none;
  border: none;
  border-radius: 75px;
  color: rgb(255, 255, 255);
  font-size: 20px;
  font-weight: 500;
  background-color: rgb(60, 60, 60);
  cursor: pointer;
}
.nav-login-cart button:hover {
  background-color: rgb(40, 40, 40);
}
.nav-cart-count {
  width: 22px;
  height: 22px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: -35px;
  margin-left: -55px;
  border-radius: 11px;
  font-size: 14px;
  background-color: red;
  color: white;
  cursor: pointer;
}

@media only screen and (max-width: 960px) {
  .nav-f-logo {
    width: 150px;
    height: 40px;
    cursor: pointer;
  }
  .nav-login-cart button {
    width: 137px;
    height: 50px;
    
  }
}
