.descriptionbox {
  margin: 120px 170px;
}
.descriptionbox-navigator {
  display: flex;
}
.descriptionbox-nav-box {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  font-weight: 600;
  width: 171px;
  height: 70px;
  border: 1px solid #d0d0d0;
}
.descriptionbox-nav-box:hover {
  cursor: pointer;
}
.descriptionbox-nav-box.fade:hover {
  color: black;
  background-color: #f0f0f0;
  cursor: pointer;
}
.descriptionbox-nav-box.fade{
    background-color: white;
    color: grey;
}
.descriptionbox-description {
  display: flex;
  flex-direction: column;
  gap: 25px;
  border: 1px solid #d0d0d0;
  padding: 48px;
  padding-bottom: 70px;
}
