.offers {
  width: 65%;
  height: 60vh;
  display: flex;
  margin: auto;
  padding: 0 140px;
  margin-bottom: 150px;
  background: linear-gradient(
    180deg,
    rgb(209, 209, 209),
    rgb(221, 221, 194) 60%
  );
}
.offers-left {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.offers-left h1 {
  color: black;
  font-size: 80px;
  font-weight: 600;
}
.offers-left p {
  color: black;
  font-size: 22px;
  font-weight: 600;
}
.offers-left button {
  width: 282px;
  height: 70px;
  border-radius: 35px;
  background-color: rgb(230, 0, 0);
  border: none;
  color: white;
  font-size: 22px;
  font-weight: 500;
  margin-top: 30px;
  cursor: pointer;
}
.offers-left button:hover {
  background-color: rgb(250, 0, 0);
}
.offers-right {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-top: 50px;
}
@media only screen and (max-width: 1440px) {
  .offers-left h1 {
    color: black;
    font-size: 60px;
    font-weight: 600;
  }
  .offers-left p {
    color: black;
    font-size: 22px;
    font-weight: 600;
  }
  .offers-left button {
    width: 202px;
    height: 70px;
    border-radius: 35px;
    background-color: rgb(230, 0, 0);
    border: none;
    color: white;
    font-size: 22px;
    font-weight: 500;
    margin-top: 30px;
    cursor: pointer;
  }
  .offers-right-img{
    height: 440px;
  }
}
