.hero {
  height: 100vh;
  background: linear-gradient(
    180deg,
    rgb(255, 255, 255) 0%,
    rgba(255, 255, 255, 0.766) 60%
  );
  display: flex;
}
.hero-left {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 20px;
  padding-left: 180px;
  line-height: 1.1;
}
.hero-left h2 {
  color: rgb(12, 12, 12);
  font-size: 26px;
  font-weight: 600;
}
.hero-left p {
  color: black;
  font-size: 80px;
  font-weight: 700;
}
.hero-hand-icon {
  display: flex;
  align-items: center;
  gap: 20px;
}
.hero-hand-icon img {
  width: 105px;
}
.hero-latest-btn button {
  justify-content: center;
  align-items: center;
  gap: 15px;
  width: 260px;
  height: 60px;
  border-radius: 75px;
  margin-top: 30px;
  background-color: rgb(230, 0, 0);
  border: none;
  color: white;
  font-size: 18px;
  font-weight: 500;
  cursor: pointer;
}
.hero-latest-btn button:hover {
  background-color: rgb(250, 0, 0);
}
.hero-right {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media only screen and (max-width: 960px){
  .hero-left h2 {
    color: rgb(12, 12, 12);
    font-size: 17px;
    font-weight: 600;
  }
  .hero-left p {
    color: black;
    font-size: 50px;
    font-weight: 700;
  }
  .hero-hand-icon img {
    width: 40px;
  }
  .hero-right img{
    width: 500px;
  }
  .hero-left {
    padding-left: 80px;
 
  }
}
