.cartitems {
  margin: 100px 170px;
}
.cartitems hr {
  height: 1px;
  background: rgb(184, 184, 184);
  border: 0;
}
.cartitems-format-main {
  display: grid;
  grid-template-columns: 0.5fr 2fr 1fr 1fr 1fr 1fr;
  align-items: center;
  gap: 75px;
  padding: 20px 0;
  color: rgb(0, 0, 0);
  font-size: 18px;
  font-weight: 600;
}
.cartitems-format {
  font-size: 17px;
  font-weight: 500;
}
.carticon-product-icon {
  height: 62px;
}
.cartitems-remove-icon {
  width: 15px;
  margin: 0 40px;
  cursor: pointer;
}
.cartitems-quantity {
  width: 64px;
  height: 50px;
  border: 2px solid rgb(220, 220, 220);
  background-color: white;
}
.cartitems-down {
  display: flex;
  margin: 100px 0;
}
.cartitems-total {
  flex: 1;
  display: flex;
  flex-direction: column;
  margin-right: 200px;
  gap: 40px;
}
.cartitems-total-item {
  display: flex;
  justify-content: space-between;
  padding: 15px 0;
}
.cartitems-total button {
  width: 262px;
  height: 58px;
  outline: none;
  border: none;
  background: #3c3c3c;
  color: white;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  border-radius: 5px;
}
.cartitems-promocode {
  flex: 1;
  font-size: 16px;
  font-weight: 500;
}
.cartitems-promobox {
  width: 504px;
  margin-top: 15px;
  padding-left: 20px;
  height: 58px;
  background-color: rgb(225, 225, 225);
  border-radius: 5px;
}
.cartitems-promobox input {
  border: none;
  outline: none;
  background-color: transparent;
  font-size: 16px;
  width: 330px;

}
.cartitems-promobox button {
  width: 170px;
  height: 58px;
  font-size: 16px;
  background-color: #3c3c3c;
  color: white;
  cursor: pointer;
  border: none;
  border-radius: 0 5px 5px 0;
}
