.relatedproducts {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  height: 90vh;
}
.relatedproducts h1 {
  color: #171717;
  font-size: 50px;
  font-weight: 600px;
}
.relatedproducts hr {
  width: 200px;
  height: 6px;
  border-radius: 10px;
  background-color: #252525;
}
.relatedproducts-item {
  margin-top: 50px;
  display: flex;
  gap: 30px;
}
@media only screen and (max-width: 1440px) {
  .relatedproducts-item {
    gap: 10px;
  }
}
